import "./App.scss";
import { Route, Link } from "react-router-dom";
import Home from "./pages/Home";

function App() {
  const defaultPath = "/portfolio_2021";
  return (
    <div className="App">
      <header className="App-header">
        <Link to="/">index</Link>
        <Link to="/portfolio_2021">portfolio_2021</Link>
        <Link to="/portfolio_2021/popular">popular</Link>
      </header>
      <Route path={defaultPath + "/"} render={(routeProps) => <div>index1</div>} />
      <Route path={defaultPath + "/popular"} render={(routeProps) => <div>popular1</div>} />
      {/* <Routes>
        <Route path={defaultPath + "/"} element={<h3>home4</h3>} />
        <Route path={defaultPath + "/popular"} element={<h3>popular4</h3>} />
      </Routes> */}
    </div>
  );
}

export default App;
